import { Component } from '@angular/core';

import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { FormControl, AbstractControl, FormBuilder, Validators, FormGroup } from '@angular/forms';
import { BikeProvider } from '../../providers/bike/bike';
import { Storage } from '@ionic/storage';
import { MenuController } from 'ionic-angular';
import {MyApp} from '../../app/app.component';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';

/**
 * Generated class for the SellbikeFourPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sellbike-four',
  templateUrl: 'sellbike-four.html',
})
export class SellbikeFourPage {
  public form: FormGroup;
  myform: FormGroup;
  responseData: any;
  error: string;
  busy: boolean;
  isChecked: boolean;
  disabled: any;

  constructor(private builder: FormBuilder,
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public navParams: NavParams,
    public menu: MenuController,
    public bikeService: BikeProvider,
    private storage: Storage,
    public loadingCtrl: LoadingController,
    public authService: AuthServiceProvider,
    private myApp:MyApp) {
      this.form = builder.group({
        'postal_code': ['', Validators.compose([Validators.required])],
      
       

      });
  }

  ionViewDidLoad() {

    if( localStorage.getItem('bike1_id'))
    {
     let dataUserDet = {
       "id": localStorage.getItem('bike1_id')
       
     };
     this.bikeService.getbikedetails(dataUserDet).subscribe(res=>{
       let details = res
       console.log(res)
       if(details.Ack == 1){
         
         this.form.controls['postal_code'].setValue(details.product.postal_code);
        
       }
       
      },err=>{
       console.log(err);
        const alert = this.alertCtrl.create({
          title: 'Bike not fetch from your account!',
          buttons: ['OK']
        });
        alert.present();
     });
 
    }

    
    console.log('ionViewDidLoad SellbikeFourPage');
  }

  Sellbikefive(fromdata) {

//  let bike1:any = JSON.parse(localStorage.getItem('bikesell1'));
if( localStorage.getItem('bike1_id'))
{
  fromdata.id = localStorage.getItem('bike1_id');
}


 if (!this.form.valid) {
   const alert = this.alertCtrl.create({
     title: 'Bike add faild!',
     subTitle: "Please fill all the details.",
     buttons: ['OK']
   });
   alert.present();
 }
 else{
   let loading = this.loadingCtrl.create({
     spinner: 'show',
     content: 'Loading Please Wait...',
     duration: 3000
   });
   loading.present();
  //console.log(fromdata);
  this.bikeService.addbike(fromdata).subscribe(res=>{
    let details = res
    if(details.ack == 1){
     localStorage.setItem('bike1_id',details.id);
   //   console.log(res);
   //    const alert = this.alertCtrl.create({
   //      title: 'Bike Added Successfully .',
   //      buttons: ['OK']
   //    });
   //  alert.present();
  // this.navCtrl.setRoot('SellbikeThreePage');
   this.navCtrl.push('BikeSuccessPage');
    }
    
   },err=>{
    //console.log(err);
     const alert = this.alertCtrl.create({
       title: 'Service Failed!',
       buttons: ['OK']
     });
     alert.present();
  });
   
 }

    //this.navCtrl.setRoot('BikeSuccessPage');
  }
}



